




















import Header from "@/components/Header/Header.vue";
import Settings from "@/components/Creat/Settings.vue";
import { ICanvas, ICoordinates } from "@/utils/types";
import Vue from "vue";
import { mapGetters } from "vuex";
export default Vue.extend({
  data(): ICanvas {
    return {
      canvas: null,
      context: null,
      colorLocal: "000000",
      sizeLocal: 5,
      action: "up",
      arrToBuildFigure: [],
      imageData: ""
    };
  },
  components: { Header, Settings },
  mounted() {
    const canvas: HTMLCanvasElement | null = document.querySelector("canvas");
    const context:
      | CanvasRenderingContext2D
      | null
      | undefined = canvas?.getContext("2d");
    this.canvas = canvas;
    this.context = context;
  },
  methods: {
    mDown(e: MouseEvent): void {
      this.action = "down";
      this.saveImageData();
      if (this.mode === "pencil") this.drawPoint(e);
    },
    mUp(): void {
      this.action = "up";
      this.arrToBuildFigure = [];
      this.saveImageData();
    },
    mMove(e: MouseEvent): void {
      if (this.action === "down") {
        if (this.mode === "pencil") {
          this.drawPoint(e);
        } else {
          const coordinates: ICoordinates = this.getCoordinates(e);
          const ctx = this.context;
          ctx.putImageData(this.imageData, 0, 0);
          let x1;
          let y1;
          if (this.arrToBuildFigure.length === 0) {
            x1 = coordinates.x;
            y1 = coordinates.y;
            this.arrToBuildFigure.push([x1, y1]);
          } else {
            [x1, y1] = [...this.arrToBuildFigure[0]];
          }
          ctx.beginPath();
          ctx.strokeStyle = this.color;
          ctx.lineWidth = this.size;
          ctx.lineCap = "round";
          if (this.mode === "line") {
            ctx.moveTo(x1, y1);
            ctx.lineTo(coordinates.x, coordinates.y);
            ctx.stroke();
          } else if (this.mode === "rectangle") {
            ctx.strokeRect(x1, y1, coordinates.x - x1, coordinates.y - y1);
          } else if (this.mode === "circle") {
            ctx.arc(
              x1,
              y1,
              coordinates.x - x1 < 0
                ? -(coordinates.x - x1)
                : coordinates.x - x1,
              0,
              2 * Math.PI
            );
            ctx.stroke();
          }
        }
      }
    },
    drawPoint(e: MouseEvent) {
      const coordinates = this.getCoordinates(e);
      const ctx = this.context;
      ctx.beginPath();
      ctx.strokeStyle = this.color;
      ctx.lineWidth = this.size;
      ctx.lineCap = "round";
      ctx.moveTo(coordinates.x, coordinates.y);
      ctx.lineTo(
        coordinates.x - coordinates.dx,
        coordinates.y - coordinates.dy
      );
      ctx.stroke();
      ctx.closePath();
    },
    saveImageData() {
      this.imageData = this.context.getImageData(
        0,
        0,
        this.canvas?.width,
        this.canvas?.height
      );
    },
    getCoordinates(e: MouseEvent): ICoordinates {
      return {
        x: e.offsetX,
        y: e.offsetY,
        dx: e.movementX,
        dy: e.movementY
      };
    }
  },
  computed: {
    ...mapGetters(["size", "color", "mode"])
  }
});
