


































import { IUserToAuth } from "@/utils/types";
import { mapActions } from "vuex";
import Vue from "vue";

export default Vue.extend({
  data(): IUserToAuth {
    return {
      userToSent: {
        userMail: "",
        userPassword: ""
      },
      submitStatus: ""
    };
  },
  methods: {
    ...mapActions(["signInUser"]),
    signInUserLocal(): void {
      this.signInUser(this.userToSent).catch(err => {
        this.submitStatus = err.message;
        setTimeout(() => {
          this.submitStatus = "";
        }, 2000);
      });
    }
  }
});
