







import { IDate } from "@/utils/types";
import Vue from "vue";
export default Vue.extend({
  data(): IDate {
    return {
      dateLocal: ""
    };
  },
  props: ["pictureInfo"],
  mounted() {
    const pictureDate: Date = new Date(+this.pictureInfo.date),
      day = pictureDate.getDate(),
      month = pictureDate.getMonth(),
      year = pictureDate.getFullYear(),
      months: Array<string> = [
        "January",
        "February",
        "March",
        "April",
        "May",
        "June",
        "July",
        "August",
        "September",
        "October",
        "November",
        "December"
      ];
    this.dateLocal = `${("" + day).length < 2 ? "0" + day : day}/${
      months[month]
    }/${year}`;
  }
});
