

























import { mapActions, mapGetters } from "vuex";
import Vue from "vue";
import { ISettings } from "@/utils/types";
export default Vue.extend({
  data(): ISettings {
    return {
      localSize: 0,
      isSizeInput: false,
      localColor: "",
      mode: "pencil"
    };
  },
  computed: {
    ...mapGetters(["size", "color"])
  },
  watch: {
    localSize(): void {
      this.setSize(this.localSize);
    },
    localColor(): void {
      this.setColor(this.localColor);
    }
  },
  methods: {
    ...mapActions(["setSize", "setColor", "setMode"]),
    setIsSizeInput(): void {
      this.isSizeInput = !this.isSizeInput;
    },
    setPencilMode(): void {
      this.mode = "pencil";
      this.setMode("pencil");
    },
    setLineMode(): void {
      this.mode = "line";
      this.setMode("line");
    },
    setCircleMode(): void {
      this.mode = "circle";
      this.setMode("circle");
    },
    setRectangleMode(): void {
      this.mode = "rectangle";
      this.setMode("rectangle");
    }
  },
  mounted() {
    this.localSize = this.size;
    this.localColor = this.color;
  }
});
